<template>
  <div class="d-flex quantity-and-btn">
    <div class="quantity-input d-flex">
      <BLink href="#" class="step-down-btn" @click.prevent="quantityDown"
        >-</BLink
      >
      <input
        type="number"
        min="1"
        :value="quantity"
        name="quantity"
        ref="inputQuantity"
        disabled
      />
      <BLink href="#" class="step-up-btn" @click.prevent="quantityUp">+</BLink>
    </div>
    <button
      @click.prevent="addToCart"
      :disabled="btnDisable || isAddToCartBtnDisabled"
      :style="buttonClass"
      class="btn btn-info product-parameter-submit-btn"
    >
      <i class="lnr lnr-cart"></i>{{ $t("add_to_cart") }}
    </button>
  </div>
</template>

<script setup>
import { computed, ref, watch } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";
import config from "@storefront/core/lib/utils/config";
import { useGtm } from "@gtm-support/vue-gtm";
import { useModal } from "bootstrap-vue-next";

const props = defineProps({
  product: Object,
  productConfig: Object,
  addedQuantity: Function,
});

const store = useStore();
const { t } = useI18n();
const gtm = useGtm();
const { show } = useModal("ProductCardModal");

const quantity = ref(1);
const cartPrices = ref(null);
const cartItems = ref(null);
const isAddToCartBtnDisabled = ref(false);

const product = computed(() => store.getters["product/getCurrentProduct"]);

watch(
  () => product.value,
  (newProduct) => {
    if (newProduct) {
      quantity.value = 1;
    }
  }
);

const btnDisable = computed(() => {
  if (props.product.__typename == "ConfigurableProduct") {
    const retval = store.getters["product/getCurrentChildSku"];
    if (retval != null) {
      return false;
    } else {
      return true;
    }
  } else {
    return false;
  }
});

const buttonClass = computed(() => {
  if (btnDisable.value == true) {
    return "cursor: not-allowed;";
  } else {
    return "";
  }
});

const quantityUp = () => {
  quantity.value++;
  props.addedQuantity(quantity.value);
};

const quantityDown = () => {
  if (quantity.value > 1) quantity.value--;
  props.addedQuantity(quantity.value);
};

const HrTrackCart = async () => {
  cartPrices.value = await store.getters["cart/getCartPrices"];
  cartItems.value = await store.getters["cart/getCartItems"];

  const hritems = [];
  cartItems.value.forEach((element) => {
    hritems.push(element.product.sku);
  });

  self.ADDWISH_PARTNER_NS.api.cart.setCart(
    {
      total: cartPrices.value?.grand_total?.value,
      productNumbers: hritems,
    },
    function () {
      //Cart has been tracked for HR
    }
  );
};

const addToCart = async () => {
  try {
    isAddToCartBtnDisabled.value = true;
    switch (props.product.__typename) {
      case "SimpleProduct": {
        const item =
          '{data: {sku: "' +
          props.product.sku +
          '", quantity:' +
          quantity.value +
          "}}  ";
        let retval = await store.dispatch("cart/addToCart", {
          type: props.product.__typename,
          item: item,
          store,
        });
        if (retval == false) {
          const msg = {
            type: "danger",
            title: t("shopping_basket"),
            text: t("not_add_product"),
          };
          store.dispatch("messages/sendMessage", { message: msg });
        } else {
          if (config.helloRetail && config.helloRetail.enabled) {
            HrTrackCart();
          }

          if (gtm.enabled()) {
            window?.dataLayer.push({
              event: "eec.add",
              ecommerce: {
                add: {
                  products: [
                    {
                      name: props.product.name,
                      id: props.product.sku,
                      quantity: quantity.value,
                    },
                  ],
                },
              },
            });
          }
          show();
        }
        break;
      }
      case "ConfigurableProduct": {
        const item =
          '{ parent_sku: "' +
          props.product.sku +
          '" data: {sku: "' +
          store.getters["product/getCurrentChildSku"] +
          '", quantity:' +
          quantity.value +
          "}}  ";
        let retval = await store.dispatch("cart/addToCart", {
          type: props.product.__typename,
          item: item,
          store,
        });
        if (retval == false) {
          const msg = {
            type: "danger",
            title: t("add_to_cart"),
            text: t("not_add_product"),
          };
          store.dispatch("messages/sendMessage", { message: msg });
        } else {
          if (config.helloRetail && config.helloRetail.enabled) {
            HrTrackCart();
          }

          if (gtm.enabled()) {
            window?.dataLayer.push({
              event: "eec.add",
              ecommerce: {
                add: {
                  products: [
                    {
                      name: props.product.name,
                      id: props.product.sku,
                      quantity: quantity.value,
                    },
                  ],
                },
              },
            });
          }
          show();
        }
        break;
      }

      default:
        break;
    }
  } catch (error) {
    console.log(error);
  } finally {
    isAddToCartBtnDisabled.value = false;
  }
};
</script>
