<template>
  <div class="product-parameters">
    <div class="short-desc" v-html="getShortDescription"></div>

    <ProductCardModal
      :product="product"
      :priceRange="priceRange"
      :quantity="quantity"
      :products="upSell"
    />
    <ProductPricing :product="product" :priceRange="priceRange" :productConfig="productConfig" />
    <ProductSelectSimple
      :parameters="parameters"
      :product="product"
      :options="options"
      :productConfig="productConfig"
    />
    <ProductStoreAvailability
      :product="product"
      :productConfig="productConfig"
      :options="options"
    />
    <ProductAddtoCart :product="product" :addedQuantity="(qty) => (quantity = qty)" />
    <div v-if="crossSell.length > 0" class="product-gallery-block">
      <h2>{{ $t("cross_sell_products") }}</h2>
      <MoreProductsCarousel :products="crossSell" />
    </div>
    <div v-if="upSell.length > 0" class="product-gallery-block">
      <h2>{{ $t("related_products") }}</h2>
      <MoreProductsCarousel :products="upSell" />
    </div>
    <ProductPaymentPartners />
    <SocialShare :product="product" />
  </div>
</template>

<script setup>
import { computed, ref } from "vue";
import { useStore } from "vuex";
import ProductCardModal from "@/base/core/components/product/ProductCardModal";
import ProductPricing from "@/base/core/components/product/ProductPricing";
import ProductSelectSimple from "@/base/core/components/product/ProductSelectSimple";
import ProductStoreAvailability from "@/base/core/components/product/ProductStoreAvailability";
import ProductAddtoCart from "@/base/core/components/product/ProductAddtoCart";
import ProductPaymentPartners from "@/base/core/components/product/ProductPaymentPartners";
import SocialShare from "@/base/core/components/product/ProductSocialShare";
import MoreProductsCarousel from "@/base/core//components/MoreProductsCarousel";

defineProps({
  parameters: { type: Object },
  isInWishList: { type: Boolean },
});

const quantity = ref(1);

const store = useStore();

const product = computed(() => store.getters["product/getCurrentProduct"]);
const productConfig = computed(() => store.getters["product/getCurrentConfig"]);
const options = computed(() => store.getters["product/getCurrentProductOptions"]);
const crossSell = computed(() => store.getters["product/getCrossSellProducts"]);
const upSell = computed(() => store.getters["product/getUpSellProducts"]);

const priceRange = computed(() => {
  if (product.value && productConfig.value && product.value.__typename == "ConfigurableProduct") {
    if (store.getters["product/getCurrentChildSku"] != null) {
      const found = productConfig.value.variants.find((v) => {
        if (v.product.sku == store.getters["product/getCurrentChildSku"]) {
          return true;
        }
      });
      if (found != null) {
        return found.product.price_range;
      }
    } else if (productConfig.value.variants.length == 1) {
      return productConfig.value.variants[0].product.price_range;
    }
    return product.value.price_range;
  }
  return product.value.price_range;
});

const getShortDescription = computed(() =>
  product.value.short_description.html
    .replace(/<p>(.*)<\/p>/gis, "$1")
    .replace(/&gt;/gs, ">")
    .replace(/&lt;/gs, "<")
);
</script>
