<template>
  <BaseLayout>
    <section class="product product-section" v-if="product">
      <div class="container">
        <ProductAlert v-model="showDismissibleAlert" :product="product" />
        <h1 class="product--name d-block" v-if="mobileCheck && !isServer">
          {{ product.name }}
        </h1>
        <div class="row pb-md-20">
          <div class="col-12 col-md-6">
            <ProductGallery
              v-if="product.media_gallery.length > 0"
              @changeIsInWishList="updateIsInWishList"
              :images="product.media_gallery"
              :isInWishList="$store.getters['user/isProductInWishlist'](product.sku)"
            />
            <ProductGallery
              v-if="product.media_gallery.length == 0"
              @changeIsInWishList="updateIsInWishList"
              :images="[product.image]"
              :isInWishList="$store.getters['user/isProductInWishlist'](product.sku)"
            />
            <DescriptionTab class="d-none d-md-block" />
          </div>
          <div class="col-12 col-md-6">
            <h1 class="product--name d-block" v-if="!mobileCheck && !isServer">
              {{ product.name }}
            </h1>
            <ProductParameters v-if="product.__typename != 'BundleProduct'" :isInWishList="false" />
            <ProductParametersBundle
              v-else-if="product.__typename == 'BundleProduct'"
              :isInWishList="false"
            />
            <BundelTips class="d-none d-md-block" />
          </div>
        </div>
        <div class="row pb-md-70 d-md-none">
          <DescriptionTab class="col-12 col-md-6" />
        </div>

        <div v-if="related.length > 0" class="product-gallery-block">
          <h2>{{ $t("up_sell_products") }}</h2>
          <MoreProductsCarousel :products="related" />
        </div>
        <div v-if="productForm != null">
          <DynamicForms :product_sku="product.sku" type="product" />
        </div>
      </div>
    </section>
    <section v-else>
      <div class="container">
        <h4 class="no-product">{{ $t("no_product_found") }}</h4>
      </div>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { useHead } from "@unhead/vue";
import { useGtm } from "@gtm-support/vue-gtm";
import { useI18n } from "vue-i18n";
import VueCookies from "vue-cookies";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import ProductAlert from "@/base/core/components/ProductAlert";
import ProductGallery from "@/base/core/components/ProductGallery";
import ProductParameters from "@/base/core/components/ProductParameters";
import ProductParametersBundle from "@/base/core/components/ProductParametersBundle";
import DescriptionTab from "@/base/core/components/DescriptionTab";
import BundelTips from "@/base/core/components/BundelTips";

import { isMobile, isTablet } from "mobile-device-detect";

import MoreProductsCarousel from "@/base/core/components/MoreProductsCarousel";
import DynamicForms from "@/base/core/components/DynamicForms";
import { isServer } from "@storefront/core/helpers";
import config from "@config";

// GTM
const gtm = useGtm();

// I18N
const { t } = useI18n();

// Set up store information using useStore
const store = useStore();

const showDismissibleAlert = ref(false);

const product = computed(() => store.getters["product/getCurrentProduct"]);
const related = computed(() => store.getters["product/getRelated"]);
const productConfig = computed(() => store.getters["product/getCurrentConfig"]);
const productForm = computed(() => store.getters["forms/getProductForm"]);
const mobileCheck = computed(() => {
  if (isMobile || isTablet) {
    return true;
  }
  return false;
});
const finalPrice = computed(() => {
  if (product.value.__typename == "ConfigurableProduct") {
    if (store.getters["product/getCurrentChildSku"] != null) {
      const found = productConfig.value.variants.find((v) => {
        if (v.product.sku == store.getters["product/getCurrentChildSku"]) {
          return true;
        }
      });
      if (found != null) {
        return found.product.price_range.minimum_price.final_price.value.toFixed(2);
      }
    }
    if (
      product.value.price_range.minimum_price.final_price.value !=
      product.value.price_range.maximum_price.final_price.value
    ) {
      return (
        product.value.price_range.minimum_price.final_price.value.toFixed(2) +
        " - " +
        product.value.price_range.maximum_price.final_price.value.toFixed(2).replace(".", ",")
      );
    }
    return product.value.price_range.minimum_price.final_price.value.toFixed(2);
  }
  if (
    product.value.price_range.minimum_price.final_price.value !=
    product.value.price_range.maximum_price.final_price.value
  ) {
    return (
      product.value.price_range.minimum_price.final_price.value.toFixed(2).replace(".", ",") +
      " - " +
      product.value.price_range.maximum_price.final_price.value.toFixed(2).replace(".", ",")
    );
  }
  return product.value.price_range.minimum_price.final_price.value.toFixed(2);
});

const regularPrice = computed(() => {
  if (product.value.__typename == "ConfigurableProduct") {
    if (store.getters["product/getCurrentChildSku"] != null) {
      const found = productConfig.value.variants.find((v) => {
        if (v.product.sku == store.getters["product/getCurrentChildSku"]) {
          return true;
        }
      });
      if (found != null) {
        return found.product.price_range.minimum_price.regular_price.value.toFixed(2);
      }
    }
    if (
      product.value.price_range.minimum_price.regular_price.value !=
      product.value.price_range.maximum_price.regular_price.value
    ) {
      return (
        product.value.price_range.minimum_price.regular_price.value.toFixed(2) +
        " - " +
        product.value.price_range.maximum_price.regular_price.value.toFixed(2).replace(".", ",")
      );
    }
    return product.value.price_range.minimum_price.regular_price.value.toFixed(2);
  }
  if (
    product.value.price_range.minimum_price.regular_price.value !=
    product.value.price_range.maximum_price.regular_price.value
  ) {
    return (
      product.value.price_range.minimum_price.regular_price.value.toFixed(2).replace(".", ",") +
      " - " +
      product.value.price_range.maximum_price.regular_price.value.toFixed(2).replace(".", ",")
    );
  }
  return product.value.price_range.minimum_price.regular_price.value.toFixed(2);
});

const metaInformation = computed(() => ({
  title: product.value
    ? product.value.meta_title
      ? product.value.meta_title
      : product.value.name ?? ""
    : "",
  keywords: product.value && product.value.meta_keyword ? product.value.meta_keyword ?? "" : "",
  description: product.value
    ? product.value.meta_description
      ? product.value.meta_description ?? ""
      : product.value.short_description ?? ""
      ? product.value.short_description.html ?? ""
      : ""
    : "",
  image: product.value ? product.value.image.url : "",
  price: product.value ? product.value.price_range.minimum_price.final_price.value ?? "" : "",
  priceCurrency: "EUR",
  pageTitle: product.value ? product.value.name ?? "" : "",
}));

// Set up meta information using useHead
useHead({
  meta: [
    {
      name: "title",
      content: () => metaInformation.value["title"],
    },
    {
      name: "keywords",
      content: () => metaInformation.value["keywords"],
    },
    {
      name: "description",
      content: () => metaInformation.value["description"],
    },
    { property: "og:type", content: "product" },
    {
      property: "og:title",
      content: () => metaInformation.value["title"],
    },
    {
      property: "og:image",
      content: () => metaInformation.value["image"],
    },
    {
      property: "og:description",
      content: () => metaInformation.value["description"],
    },
    {
      property: "product:price:amount",
      content: () => metaInformation.value["price"],
    },
    {
      property: "product:price:currency",
      content: () => metaInformation.value["priceCurrency"],
    },
  ],
  title: () => metaInformation.value["pageTitle"],
});

const updateGTM = () => {
  if (!isServer) {
    if (gtm.enabled() && product.value.name) {
      window?.dataLayer.push({
        event: "eec.detail",
        ecommerce: {
          detail: {
            currency: "EUR",
            value: finalPrice.value,
            products: [
              {
                name: product.value.name,
                id: product.value.sku,
                price: regularPrice.value,
                discount: (regularPrice.value - finalPrice.value).toFixed(2),
              },
            ],
          },
        },
      });
    }
  }
};

watch(product, updateGTM, { immediate: true });

const updateIsInWishList = () => {
  if (store.getters["user/getIsLoggedIn"] == true) {
    if (store.getters["user/isProductInWishlist"](product.value.sku) == true) {
      store.dispatch("user/setProductWishlistStatus", {
        sku: product.value.sku,
        parentSku: null,
        store: store,
      });
    } else {
      if (product.value.__typename == "SimpleProduct") {
        store.dispatch("user/setProductWishlistStatus", {
          sku: product.value.sku,
          parentSku: null,
          store: store,
        });
      } else {
        const retval = store.getters["product/getCurrentChildSku"];
        if (retval == null) {
          const msg = {
            type: "danger",
            title: t("wishlist error"),
            text: t("wishlist_select_options"),
          };
          store.dispatch("messages/sendMessage", { message: msg });
          //this.$router.push("/" + product.value.url_key);
        } else {
          store.dispatch("user/setProductWishlistStatus", {
            sku: store.getters["product/getCurrentChildSku"],
            parentSku: product.value.sku,
            store: store,
          });
        }
      }
    }
  } else {
    const msg = {
      type: "danger",
      title: t("wishlist error"),
      text: t("wishlist_logged_in"),
    };
    store.dispatch("messages/sendMessage", { message: msg });
  }
};

const updateBreadcrumbs = () => {
  if (product.value) {
    const current = product.value.name;
    let breadcrumb = { current, routes: [] };
    if (store.state.route.from.path != "/") {
      // if (this.category) {
      //   let retval = product.value.categories.find((i) => i.id === this.category.id);
      //   if (retval !== false) {
      //     const { breadcrumbs } = this.category;
      //     if (breadcrumbs != null) {
      //       breadcrumbs.sort((a, b) => {
      //         if (a.category_level < b.category_level) return -1;
      //         if (a.category_level > b.category_level) return 1;
      //         return 0;
      //       });
      //       let path = "";
      //       breadcrumbs.forEach((element) => {
      //         if (path.length > 0) {
      //           path = path + "/";
      //         }
      //         path = path + element.category_url_key;
      //         breadcrumb.routes.push({
      //           name: element.category_name,
      //           route_link: "/" + path,
      //         });
      //       });
      //     }
      //     breadcrumb.routes.push({
      //       name: this.category.name,
      //       route_link: this.category.url_path,
      //     });
      //   }
      // }
    }
    store.commit("breadcrumbs/set", breadcrumb);
  }
};

onMounted(async () => {
  updateBreadcrumbs();
  if (config.helloRetail && config.helloRetail.enabled) {
    const hello_retail_id = VueCookies.get("hello_retail_id");
    await store.dispatch("product/loadCrossSellProducts", {
      sku: product.value.sku,
      hello_retail_id,
    });
    await store.dispatch("product/loadUpSellProducts", {
      sku: product.value.sku,
      hello_retail_id,
    });
  } else {
    await store.dispatch("product/loadCrossSell", {
      sku: product.value.sku,
    });
    await store.dispatch("product/loadUpSell", {
      sku: product.value.sku,
    });
  }
  if (!isServer) {
    if (gtm.enabled()) {
      window.dataLayer.push({
        event: "eec.detail",
        ecommerce: {
          detail: {
            currency: "EUR",
            value: finalPrice.value,
            products: [
              {
                name: product.value.name,
                id: product.value.sku,
                price: regularPrice.value,
                discount: (regularPrice.value - finalPrice.value).toFixed(2),
              },
            ],
          },
        },
      });
    }
  }
});
</script>
