<template>
  <div class="product-gallery">
    <div
      class="selected-image-holder d-none d-md-block"
      @click.prevent="showMultiple"
    >
      <b-link href="#" @click.stop="toggleIsInWishList">
        <i class="lnr lnr-heart"></i>
        <div class="heart-icon" v-if="isInWishList">
          <img src="@/base/assets/heart.png" alt="heart" />
        </div>
      </b-link>
      <div
        class="selected-image"
        :style="{ backgroundImage: `url(${selectedImage})` }"
      ></div>
    </div>
    <template v-if="isClient">
      <Lightbox
        :visible="visible"
        :imgs="imgs"
        :index="index"
        @hide="handleHide"
      ></Lightbox>
      <Carousel
        class="carousel"
        :autoplay="0"
        :itemsToShow="1"
        :breakpoints="breakpoints"
      >
        <Slide v-for="(image, i) of images" :key="`${i}`" :id="`slide ${i}`">
          <b-link
            href="#"
            @click="toggleIsInWishList"
            class="mobile-add-to-wishlist"
          >
            <i class="lnr lnr-heart"></i>
            <div class="heart-icon" v-if="isInWishList">
              <img src="@/base/assets/heart.png" alt="heart" />
            </div>
          </b-link>
          <b-link
            href="#"
            class="slide-img-holder"
            @click.prevent="clickSlideGallery"
          >
            <div
              class="slide-img"
              :style="{
                backgroundImage: `url(${
                  is_mobile ? image.large : image.small
                })`,
              }"
            ></div>
          </b-link>
        </Slide>

        <template
          #addons
          v-if="
            (is_mobile && images.length > 1) ||
            (!is_mobile && images.length > 2)
          "
        >
          <Navigation>
            <template #prev>
              <i class="lnr lnr-chevron-left"></i>
            </template>
            <template #next>
              <i class="lnr lnr-chevron-right"></i>
            </template>
          </Navigation>
        </template>
      </Carousel>
    </template>
  </div>
</template>

<script setup>
import { computed, onBeforeUpdate, onMounted, ref } from "vue";
import { isMobile, isTablet } from "mobile-device-detect";
import { Carousel, Slide, Navigation } from "vue3-carousel/dist/carousel.es.js";
import Lightbox from "vue-easy-lightbox";

const props = defineProps(["images", "isInWishList"]);
const emit = defineEmits(["changeIsInWishList"]);

const isClient = ref(false);

const breakpoints = {
  // 768px and up
  768: {
    itemsToShow: 3,
  },
};
const selectedImage = ref(props.images[0].large);
const visible = ref(false);
const index = ref(0);
const slideNumber = ref(0);

onBeforeUpdate(() => {
  if (![...props.images].some(({ large }) => large == selectedImage.value))
    selectedImage.value = props.images[0]?.large;
});

const imgs = computed({
  get() {
    const data = [];
    props.images.forEach((element) => {
      data.push(element.x_large);
    });
    return data;
  },
  set() {
    //Nothing to do
  },
});
const is_mobile = computed(() => isMobile && !isTablet);

// Methods
const show = () => {
  visible.value = true;
};
const handleHide = () => {
  visible.value = false;
};
const toggleIsInWishList = () => {
  emit("changeIsInWishList");
};
const showMultiple = () => {
  imgs.value = props.images;
  index.value = slideNumber.value;
  show();
};
const changeSelectedImage = (e) => {
  const sn = e.target.parentElement.id.replace(/^\D+/g, "");
  slideNumber.value = Number(sn);
  selectedImage.value = props.images[sn].large;
};

const clickSlideGallery = (e) => {
  changeSelectedImage(e);
  if (window.innerWidth < 768) {
    showMultiple();
  }
};

onMounted(() => {
  isClient.value = true;
});
</script>

<style scoped>
:deep(.carousel__prev) {
  font-family: "system";
  padding: 8px;
  margin-right: -8px;
  font-size: 16px;
}

:deep(.carousel__next) {
  font-family: "system";
  padding: 8px;
  margin-left: -8px;
  font-size: 16px;
}
@media (min-width: 1024px) {
  :deep(.carousel__prev) {
    transform: translateY(-50%) translateX(-100%);
  }

  :deep(.carousel__next) {
    transform: translateY(-50%) translateX(100%);
  }
}
/* :deep(.carousel__track) {
  transform: translate(0px, 0px) !important;
} */
</style>
